a(0, 0, 139, 0.9);\n}\n\n.host-list-collapse {\n    position: absolute;\n    top: -0.85em;\n    right: -0.85em;\n    /* aliceblue */\n    color: rgba(240, 248, 255, 0.618);\n}\n\n.traceroute-map-editor a {\n    color: darkblue;\n}\n\n.map-indicator {\n    font-size: 1.1em;\n    margin: 4px 0 0 0;\n    padding: 6px 12px;\n    border-radius: 2px;\n    display: inline-block;\n}\n\n.map-indicator>:first-child {\n    margin-right: 0.5em;\n}\n\n.map-indicator.error {\n    /* 500 */\n    font-weight: bold;\n    color: #ffffff;\n    background: #e02f44;\n}\n\n.map-indicator.info {\n    font-weight: 500;\n    color: black;\n    background: rgba(240, 248, 255, 0.618);\n    /* color: #ffffff;\n    background: rgba(0, 0, 0, 0) linear-gradient(90deg, rgb(50, 116, 217), rgb(50, 116, 217)) repeat scroll 0% 0%; */\n}\n\n.map-indicator.loading {\n    color: black;\n    background: rgba(240, 248, 255, 0.618);\n    font-size: 1.1em;\n}\n\n.icon-right-space {\n    margin-right: 8px;\n}\n\n.hspace {\n    margin-left: 0.5em;\n    margin-right: 0.5em;\n}\n\na.decorated {\n    text-decoration: underline dotted;\n}\n\n.point-popup .region-label, .point-popup .host-dest-label {\n    text-align: center;\n}\n\n.point-popup hr {\n    margin: 0.55em auto;\n}\n\n.point-popup .hop-nth {\n    font-style: italic;\n    margin-right: 0.32em;\n}\n\n.point-popup .hop-entry {\n    display: flex;\n    flex-direction: row;\n    line-height: 1.2;\n}\n\n.point-popup .hop-entry:not(:last-child) {\n    margin-bottom: 0.3em;\n}\n\n.point-popup .hop-detail {\n    display: flex;\n    flex-direction: row;\n    flex-wrap: wrap;\n    justify-content: space-between;\n    flex-grow: 1;\n    /* Some WTF dirty fix for solving hop-detail overflows the width of hop-entry.\n        ref: https://stackoverflow.com/questions/38223879/white-space-nowrap-breaks-flexbox-layout */\n    min-width: 0;\n    margin-right: -0.15em;\n}\n\n.point-popup .hop-ip {\n    user-select: all;\n}\n\n.point-popup .hop-label, .point-popup .hop-ip {\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n.point-popup .hop-ip:not(:last-child) {\n    margin-right: 0.25em;\n}\n\n.point-popup .hop-ip-wrapper, .point-popup .hop-label {\n    margin-right: 0.15em;\n}\n\n.leaflet-popup-content {\n    /* It seems that leaflet calculates popup width in tricky ways, preventing flex elements from stretching its content width.\n        Here is a probably dirty fix. (TODO: compatibility?)\n        ref: https://github.com/Leaflet/Leaflet/blob/bc918d4bdc2ba189807bc207c77080fb41ecc196/src/layer/Popup.js#L211 */\n    width: max-content !important;\n    max-width: 19.5em;\n}\n\n.point-popup .region-label {\n    font-size: 1.1em;\n}\n\n.point-popup .hop-ip-wrapper {\n    display: flex;\n    justify-content: space-between;\n    flex-grow: 1;\n}\n\n.route-tooltip {\n    display: flex;\n    flex-direction: column;\n}\n\n.route-tooltip > * {\n    flex-grow: 1;\n}\n\n.route-tooltip > .hops-abstract {\n    display: flex;\n    justify-content: center;\n}\n\n.route-tooltip > .hops-abstract > .content {\n    max-width: 18em;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n\n.route-tooltip > .host-dest-label {\n    display: flex;\n    justify-content: center;\n}\n\n.route-tooltip > .host-dest-label > .host-label, .route-tooltip > .host-dest-label > .dest-label {\n    max-width: 12em;\n    white-space: nowrap;\n    overflow: hidden;\n    text-overflow: ellipsis;\n}\n"],"sourceRoot":""}]);
